.Item {
  display: flex;
  flex-direction: column;
  flex-basis: 300px;
  flex-shrink: 0;
  flex-grow: 0;
  max-width: 100%;
  background-color: #ffffff;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  margin: 1rem;

}
.Item:hover {
  box-shadow: 0 5px 10px 5px rgb(255, 102, 0, 0.5);
  cursor: pointer;
}
.Item a{
  text-decoration: none;
  color: #000000;
}

.Item-img {
  padding: 5px;
}
.Item-img img {
  max-width: 400px;
  width: 100%;
  height: auto;
  object-fit: cover;
}
.Item-body {
  padding: 1rem;
}
.Item-title {
  font-size: 1.25rem;
  line-height: 1.33;
  font-weight: 700;
  margin: 5px;
}
.Item-intro{
  margin: 5px;
}
.Item-stock{
  margin: 5px;
  color: #9E9E9E;
  font-weight: 300;
}
.Item-price {
  font-weight: 500;
  color: #ff6600;
  font-size: 1.5rem;
  margin: 5px;
}
